.App {
  text-align: center;
  height: 100%;
}

.container {
  display: grid;
  max-width: 800px;
  min-height: 500px;
  margin: auto;
  grid-template-columns: 2fr 2fr;
/*  grid-column-gap: 10px;
*/  grid-template-areas:
    "a b"
  ;
  padding-bottom: 8px;
  border: 8px;

}

.leftSection, .rightSection {
    padding: 8px;

}

.leftSection {
  grid-area: a;
  /* background-color: lightgreen; */
}

.rightSection {
  grid-area: b;
  /* background-color: cornflowerblue; */
}



